import { Link } from 'react-router-dom';
import styles from './form.module.css';
import { useSelector } from 'react-redux';
import { getFormErrorSelector } from '../services/form/selectors';
import useForm from '../hooks/useForm';
import { login } from '../services/login/actions';
import Alert from '../components/alert/alert';
import FormInput from '../components/form/form-input';
import SubmitButton from '../components/form/submit-button';
import { useNavigate } from 'react-router-dom';
import { FC } from 'react';
import { TFormData } from '../types/form';
import { useDispatch } from '../types';

const Login: FC = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const error = useSelector(getFormErrorSelector);

    const { field, handleSubmit } = useForm();

    const onSubmit = (form: TFormData) => {
        dispatch(login(form));
    }

    return (
        <>
            {error && <Alert message={error} type="danger" />}

            <form onSubmit={handleSubmit(onSubmit)} className={`${styles.form}`}>
                <h1></h1>

                <FormInput {...field('email')} type="email" placeholder="E-mail" />
                <FormInput {...field('password')} type="password" placeholder="" icon="HideIcon" />

                <SubmitButton type="primary"></SubmitButton>

                <p className="text text_type_main-default mt-20">
                     -  ? <Link to="/register"></Link></p>
                <p className="text text_type_main-default mt-4">
                     ? <Link to="/forgot-password"> </Link></p>
            </form>
        </>
    );
}

export default Login;